<template>
  <div class="app">
    <audio id="player"/>
    <main class="main">
      <div class="container-fluid nopadding">
        <router-view></router-view>
      </div>
    </main>
  </div>
</template>

<script>
import nodemixin from './mixins/nodemixins'
import EventBus from './utils/eventBus'
import { getJSONfromS3 } from './services/jsonService'

let player

export default {
  name: 'App',
  mixins: [nodemixin],
  mounted () {
    EventBus.$on('initAudio', function (payLoad) {
      player = document.getElementById('player')
      player.play()
    })
    EventBus.$on('playAudio', function (input) {
      player.src = 'https://s3-eu-west-1.amazonaws.com/' + process.env.RESOURCEBUCKET + '/public/' + input
      player.play()
    })
  },
  created: function () {
    getJSONfromS3()
      .then(res => {
        this.node = res
        const target = this.node.audio[this.languageIndex]
        target ? EventBus.$emit('playAudio', target) : {}
    })
  }
}
</script>

<style>
.nopadding {
  padding-left: 0px;
  padding: 0px;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 98vw;
  height: 100vh;
}
body {
  background-image: url(https://i.pinimg.com/originals/5b/be/e9/5bbee9b08f6d1eafb014269ac137ae1f.jpg);
  background-size: cover;
}
.bg {
  height: 100vh;

  background-repeat: no-repeat;
  background-position: bottom right;
  background-size: 75% 50%;
}
#controls {
  position: absolute;
  bottom: 120px;
  right: 20px;
}
#control-row {
  height: 55px;
}
#direction {
  width: 55px;
  float: left;
}
#soundarea {
  position: absolute;
  bottom: 200px;
  left: 4vw;
}
#info {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
}
.language-btn {
  left: 20px;
  outline: none;
  position: absolute;
  bottom: 130px;
  height: 17vw;
  max-height: 110px;
}
</style>



